// DEMO PAGE

html {
  font-family: sans-serif;
  text-size-adjust: 100%;
  background-color: #DDD; }

body {
  font: 100%/1.5 Arial;
  -webkit-font-smoothing: antialiased;
  padding: 0;
  margin: 0; }

article {
  font-size: 80%;
  color: #333;
  padding: 2em 4em; }

code {
  font-size: 130%; }

section {
  li {
    margin: .25em 0; } }

