.sm-item-useraccount {
  background: #73BE13;
  color: #FFF;
  font-size: 2em;
  font-weight: 300;
  margin: 0;
  padding: .5em;
  overflow: hidden; }

.sm-useraccount-photo {
  float: left;
  width: 50px;
  border: solid 3px rgba(0, 0, 0, 0.2);
  border-radius: 3px; }

.sm-useraccount-name {
  padding: .7em 1em;
  margin-left: 50px; }

.fb:hover {
  background-color: #3b5998;
  color: #FFF; }

.tw:hover {
  background-color: #4099ff;
  color: #FFF; }

.gp:hover {
  background-color: #d34836;
